/* Large desktops */

body,
html {
  width: 100%;
  height: 100%;
}

main {
  position: relative;
  margin-bottom: 82px;
  padding-top: 88px;
}

html > div[style] {
  display: none !important;
}

ul,
menu,
ol,
section,
figure,
aside {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

.wrapper {
  display: block;
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 60px;
}

.page-breadcrumbs {
  margin-bottom: 24px;
  padding: 0;
  font-size: 14px;
}

.page-breadcrumbs a,
.page-breadcrumbs strong {
  margin: 0 10px;
}

.page-breadcrumbs > span > span > a {
  margin-left: 0;
}

.page-breadcrumbs strong {
  margin-right: 0;
}

.stop-scrolling {
  position: fixed;
  width: 100%;
  height: 100%;
  max-height: 100%;
  overflow-y: scroll;
}

body,
html {
  color: var(--color-dark-primary);
  font-size: 16px;
  font-weight: normal;
  font-family: var(--main-font);
  line-height: 24px;
  -webkit-font-smoothing: antialiased;
}

h1 {
  margin: 72px 0 26px 0;
  padding: 0;
  font-family: var(--second-font);
  font-size: 24px;
  line-height: 32px;
}

h2 {
  margin: 24px 0 14px 0;
  padding: 0;
  font-family: var(--second-font);
  font-size: 20px;
  line-height: 30px;
}

h3 {
  margin: 24px 0 14px 0;
  padding: 0;
  font-family: var(--second-font);
  font-size: 18px;
  line-height: 24px;
}

h4 {
  margin: 24px 0 14px 0;
  padding: 0;
  font-family: var(--second-font);
  font-size: 16px;
  line-height: 24px;
}

h5 {
  margin: 24px 0 14px 0;
  padding: 0;
  font-family: var(--second-font);
  font-size: 18px;
  line-height: 24px;
}

h6 {
  margin: 24px 0 14px 0;
  padding: 0;
  font-family: var(--main-font);
  font-size: 16px;
  line-height: 20px;
}

.hero__headline {
  margin: 72px 0 26px 0;
  padding: 0;
  font-family: var(--second-font);
  font-size: 24px;
  line-height: 32px;
}

strong {
  font-family: var(--second-font);
  font-size: 16px;
  line-height: 20px;
}

small {
  margin: 16px 0;
  padding: 0;
  font-size: 13px;
  line-height: 22px;
}

p {
  margin: 14px 0;
  padding: 0;
}

p:last-child {
  margin-bottom: 0;
}

a {
  margin: 0;
  padding: 0;
}

a,
a:active,
a:visited {
  position: relative;
  color: var(--color-blue-primary);
  text-decoration: none;
  transition: color var(--transition-duration-primary) var(--transition-function-primary);
}

a.link_underline,
a:active.link_underline,
a:visited.link_underline {
  padding-bottom: 2px;
  color: var(--color-blue-primary) !important;
  font-family: var(--second-font);
  font-size: 16px;
  line-height: 20px;
  text-overflow: ellipsis;
  text-shadow: none;
  background: linear-gradient(90deg, var(--color-blue-primary), var(--color-blue-secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  box-decoration-break: clone;
}

a.link_underline:after,
a:active.link_underline:after,
a:visited.link_underline:after {
  position: absolute;
  bottom: 1px;
  left: 0;
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, var(--color-blue-primary), var(--color-blue-secondary));
  transition: width var(--transition-duration-primary) var(--transition-function-secondary);
  content: "";
}

a:hover {
  color: var(--color-blue-secondary);
}

a:hover.link_underline:after {
  width: 100%;
}

h1:first-child,
h2:first-child,
h3:first-child,
h4:first-child,
h5:first-child,
h6:first-child {
  margin-top: 0;
  margin-left: 0;
}

h1:last-child,
h2:last-child,
h3:last-child,
h4:last-child,
h5:last-child,
h6:last-child {
  margin-right: 0;
  margin-bottom: 0;
}

