.button-burger {
  position: relative;
  padding: 20px;
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

.button-burger:hover .button-burger__lines,
.button-burger:hover .button-burger__lines::before,
.button-burger:hover .button-burger__lines::after {
  background: var(--color-blue-secondary);
}

.button-burger__lines {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 2px;
  margin-top: -1px;
  margin-left: -10px;
  background: var(--color-light-primary);
  transition: transform var(--transition-duration-primary) var(--transition-function-primary);
}

.button-burger__lines::before,
.button-burger__lines::after {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 2px;
  margin-top: -1px;
  margin-left: -10px;
  background: var(--color-light-primary);
  transition: transform var(--transition-duration-primary) var(--transition-function-primary);
}

.button-burger__lines::before {
  transform: translateY(-6px);
  content: '';
}

.button-burger__lines::after {
  transform: translateY(6px);
  content: '';
}

.button-burger_active .button-burger__lines {
  background: transparent !important;
}

.button-burger_active .button-burger__lines::before {
  transform: rotate(45deg);
}

.button-burger_active .button-burger__lines::after {
  transform: rotate(-45deg);
}

