.modal__close {
  position: absolute;
  top: 16px;
  right: 16px;
  width: 32px;
  height: 32px;
  background: var(--color-light-primary);
  border-radius: 50%;
  box-shadow: 0 2px 7px 1px rgba(0, 0, 0, 0.6);
  cursor: pointer;
  transition: all var(--transition-duration-primary) var(--transition-function-primary);
  content: '';
}

.modal__close::before,
.modal__close::after {
  position: absolute;
  top: 15px;
  left: 6px;
  width: 20px;
  height: 2px;
  background: var(--color-blue-primary);
  transform: rotate(45deg);
  transition: all var(--transition-duration-primary) var(--transition-function-primary);
  content: '';
}

.modal__close::after {
  transform: rotate(135deg);
}

.modal__close:hover {
  background: var(--color-blue-secondary);
}

.modal__close:hover::before,
.modal__close:hover::after {
  background: var(--color-light-primary);
}

.modal__preloader {
  left: 50% !important;
}

.preloader_cubic {
  position: absolute;
  top: 50%;
  left: 0;
  width: 60px;
  height: 60px;
  margin: auto;
  background: var(--color-light-primary);
  border: 2px solid var(--color-blue-primary);
  transform: translate(-50%, -50%);
}

.preloader_cubic:after {
  position: absolute;
  top: 37.5%;
  left: 37.5%;
  width: 25%;
  height: 25%;
  background: var(--color-blue-primary);
  animation: 2s ease-out infinite both preloader;
  content: "";
}

@keyframes preloader {
  0%, 10%, 90%, 100% {
    transform: translate3d(0, 0, 0) scale(0) rotate(180deg);
  }

  20%, 70% {
    transform: translate3d(0, 0, 0) scale(1);
  }

  30% {
    transform: translate3d(-100%, -100%, 0) scale(1);
  }

  40% {
    transform: translate3d(100%, -100%, 0) scale(1);
  }

  50% {
    transform: translate3d(100%, 100%, 0) scale(1);
  }

  60% {
    transform: translate3d(-100%, 100%, 0) scale(1);
  }
}

